const NavDrawer = styled("div")`
  width: 100vw;
  min-height: 100vh;
  position: relative;
  overflow: auto;
  @media (max-width: 400px) {
    margin: 0;
  }
`;

const Close = styled("div")`
  position: absolute;
  top: 15px;
  right: 20px;
  padding: 8px 10px;
  height: 20px;
  width: 16px;
  background: rgba(105, 131, 142, 0.35);
  border-radius: 8px;
  font-size: 40px;
  cursor: pointer;
  color: white;
  font-weight: 300;
`;

const NavItems = styled("div")`
  margin-left: 20px;
  margin-top: 20px;
  text-transform: capitalize;
  & a {
    color: white;
    margin: auto;
    text-decoration: none;
    & p {
      margin: 0;
      padding: 10px 0;
      // border-bottom: 1px solid rgba(255, 255, 255, 0.25);
    }
  }
`;

const ImgContainer = styled("div")`
  min-width: 125px;
  .logoMain {
    opacity: 1;
    transition: 0.5s;
  }
  .logoHidden {
    opacity: 0;
    transition: 0.5s;
  }
  &:hover {
    .logoMain {
      opacity: 0;
      transition: 0.5s;
    }
    .logoHidden {
      opacity: 1;
      transition: 0.5s;
    }
  }
`;

const EnterApp = styled("div")`
  margin-left:20px;
  @media (max-width: 900px) {
    .button-link {
      color: white;
      margin-left: 2rem;
    }
    .ml-form-embed {
      font-size: 14px;
    }
  }
`;

// Nav links
const NavLinks = (props) => {
  return (
    <Link
      className="button-link"
      onClick={props.func}
      to={props.link}
      spy={true}
      smooth={true}
    >
      <p>{props.title}</p>
    </Link>
  );
};

export default function Navbar() {
  const { language, width } = useApp();
  // const [anchorEl, setAnchorEl] = React.useState(null);
  // const open = Boolean(anchorEl);
  const text = textContext[language].navbar;

  // Hamburger controls
  const [nav, setNav] = React.useState(false);

  // Drawer & navbar onclick handler
  const toggleDrawer = (event) => {
    if (
      event &&
      event.type === "keydown" &&
      (event.key === "Tab" || event.key === "Shift")
    ) {
      return;
    }
    setNav(!nav);
  };

  let location = useLocation();
  let navigate = useNavigate();

  const handleHome = (l) => {
    if (l.href) {
      window.open(l.href, '_blank', 'noopener,noreferrer')
    } else {
      const link = l.link
      if (location.pathname !== "/") {
        navigate("/");
        nav && toggleDrawer();
        setTimeout(() => {
          let element = document.getElementById(link);
          element.scrollIntoView({ behavior: "smooth" });
        }, 500);
      }
      nav && toggleDrawer();
    }

  };

  // Handlers for dropdown
  //  const handleClick = (event) => {
  //    setAnchorEl(event.currentTarget);
  //  };
  //  const handleClose = () => {
  //    setAnchorEl(null);
  //  };

  // const handleChange = (event) => {
  // 	let val = event.target.getAttribute('value');
  // 	langSelect(val);
  // 	handleClose();
  // };

  // Navbar items
  const Items = (
    <NavDrawer>
      <img src={logo} alt="aperture finance" width="125" style={{marginTop:'15px', marginLeft:'20px'}}/>
      <Close onClick={toggleDrawer}>
        <div style={{marginLeft:'-4px', marginTop:'-19px'}}>
          &times;
        </div>
      </Close>
      <NavItems onClick={toggleDrawer}>
        {text.list.map((l, i) => (<>
          <NavLinks
            func={() => handleHome(l)}
            link={l.link}
            key={i}
            title={l.title}
          />
        </>))}
      </NavItems>
      <br/>
      <EnterApp>
        <a
          className="linkClass"
          href="https://app.aperture.finance"
          target="_blank"
          rel="noreferrer"
        >
          <DisabledButton text={text.button} />
        </a>
      </EnterApp>
    </NavDrawer>
  );

  const mobileNav = (
    <nav id="mobileNav">
      <div className="container">
        <Link spy={true} smooth={true} onClick={handleHome} to="home">
          <img src={logo} alt="aperture finance" width="125" />
        </Link>
        <div style={{ display: "flex", alignItems: "center" }}>
          <MenuIcon
            onClick={toggleDrawer}
            sx={{ marginLeft: "1rem", color: "white", cursor: "pointer" }}
          />
        </div>
        <Drawer anchor="right" open={nav} onClose={toggleDrawer}>
          {Items}
        </Drawer>
      </div>
    </nav>
  );

  React.useEffect(() => {
    width < 900 && document.body.classList.remove("overlay");
  }, [width]);

  return (
    <React.Fragment>
      {width < 900 ? (
        mobileNav
      ) : (
        <nav>
          <div id="navbar" className="container">
            <ImgContainer>
              <Link
                spy={true}
                smooth={true}
                style={{ cursor: "pointer" }}
                onClick={handleHome}
                to="home"
              >
                <img
                  // className="logoMain"
                  src={logo}
                  alt="aperture finance"
                  width="125"
                />
              </Link>
            </ImgContainer>
            <div className="linkContainer">
              {text.list.map((l, i) => (
                <Link
                  key={i}
                  to={l.link}
                  onClick={() => handleHome(l)}
                  spy={true}
                  smooth={true}
                >
                  <p className="linkClass">{l.title}</p>
                </Link>
              ))}
              <div className="navButton">
                <EnterApp>
                  <a
                    className="linkClass"
                    href="https://app.aperture.finance"
                    target="_blank"
                    rel="noreferrer"
                  >
                    <DisabledButton text={text.button}/>
                  </a>
                </EnterApp>
              </div>
            </div>
            {/*

						// Launch app button and Language switcher

						<div>
							<AppButton text={text.button} />
							<div
					      id="languageButton"
					      aria-controls="language-menu"
					      aria-haspopup="true"
					      aria-expanded={open ? 'true' : undefined}
					      onClick={handleClick}
					    >
					    	<img src={languageLogo} alt="language" width="16" />
					    	<p>{language}</p>
							</div>
							<Menu
								id="basic-menu"
								anchorEl={anchorEl}
								open={open}
								onClose={handleClose}
								MenuListProps={{
								'aria-labelledby': 'basic-button',
								}}
							>
								<MenuItem value='english' onClick={handleChange}>english</MenuItem>
							</Menu>
						</div>
						*/}
          </div>
        </nav>
      )}
    </React.Fragment>
  );
}
