import React from 'react'
import { useApp } from '../../context/Context';
import textContext from '../../textContext';
import { celestial } from '../../assets/images';
import Grid from '@mui/material/Grid';

// Comps
import TextBox from './TextBox';

export default function Value() {

	const { language, width } = useApp();
  const text = textContext[language].value;

	return (
		<div style={{ background: '#131122', zIndex: 1 }}>
			<div id="value" className="textContainer padding">
				{
					width > 900 &&
					<div className="celestialImg">
						<img src={celestial} alt="celestial" />
					</div>
				}
				<div className="textContainer">
					<Grid container justifyContent="space-between">
						{
							text.map((t, i) => (
								<Grid item xs={12} md={6} key={i}>
									<TextBox width={width} i={i+1} t={t} />
								</Grid>
							))
						}
					</Grid>
				</div>
			</div>
		</div>
	)
}
