import React from 'react';
import styled from 'styled-components';
import { useApp } from '../../context/Context';
import textContext from '../../textContext';
import download from '../../assets/images/download.webp';
import copy from '../../assets/images/copy.webp';
import { png1, png2, png3, png4, png5, png6, png7, png8, png9, png10, png11 } from '../../assets/images/mediakit/png';
import { svg1, svg2, svg3, svg4, svg5, svg6, svg7, svg8, svg9, svg10, svg11 } from '../../assets/images/mediakit/svg';

const Body = styled('div')`
	p {
		margin-top: 2rem;
		text-align: left;
	}
	& > div {
		width: 100%;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		.imgClass {
			padding: 1rem 0;
			a {
				color: white;
				text-decoration: none;
				margin-left: .5rem;
				vertical-align: super;
			}
			& > img {
				background-color: #2f2e3d;
				width: 100%;
			}
		}
		@media (max-width: 750px) {
			flex-wrap: nowrap;
    	display: block;
    	.imgClass {
    		width: 100%!important;
    	}
		}
	}
`;

export default function MediaKit() {

	const { language } = useApp();
	const text = textContext[language].mediakit;

	const images = [
		[{ png: png1, svg: svg1 }, { png: png2, svg: svg2 }, { png: png3, svg: svg3 }, { png: png4, svg: svg4 }],
		[{ png: png5, svg: svg5 }, { png: png6, svg: svg6 }, { png: png7, svg: svg7 }, { png: png8, svg: svg8 }],
		[{ png: png9, svg: svg9 }, { png: png10, svg: svg10 }, { png: png11, svg: svg11 }],
		[['#7534FF', '#30E6FF'], ['#000000'], ['#FFFFFF']]
	]

	const handleCopy = (text) => {
		navigator.clipboard.writeText(text);
	}

	return (
		<div id="mediakit">
			<div className="textContainer padding">
				<p className="textHeader">{text.title}</p>
				{
					text.list.map((l, i) => (
						<Body key={i}>
							<p className="subHeader">{l}</p>
							<div>
								{
									i !== 3 ?
										images[i].map((d, k) => (
											<div className="imgClass" style={images[i].length === 4 ? { width: '47.5%' } : { width: '30%' }}>
												<img src={d.png} alt="aperture finance media" />
												<div>
													<img src={download} width="27.5" alt="download" />
													<a download href={d.png}>PNG</a>
													<a download href={d.svg}>SVG</a>
												</div>
											</div>
										)) : (
											<div className="logoColorClass">
												{
													images[i].map((m, key) => (
														m.length > 1 ? (
															<div>
																<div className="colorClass" style={{ background: `linear-gradient(249.11deg, ${m[1]} 1.28%, #3ACBFF 11.93%, ${m[0]} 84.2%)` }} />
																<div style={{ display: 'flex', justifyContent: 'space-between' }}>
																	<span onClick={() => handleCopy(m[0])}>
																		<img width="21" src={copy} alt="copy" />
																		<p>{m[0]}</p>
																	</span>
																	<span onClick={() => handleCopy(m[1])}>
																		<img width="21" src={copy} alt="copy" />
																		<p>{m[1]}</p>
																	</span>
																</div>
															</div>
														) : (
															<div>
																<div className="colorClass" style={{ backgroundColor: m }} />
																<span onClick={() => handleCopy(m)}>
																	<img width="21" src={copy} alt="copy" />
																	<p>{m}</p>
																</span>
															</div>
														)
													))
												}
											</div>
										)}
							</div>
						</Body>
					))
				}
			</div>
		</div>
	)
}