import { createGlobalStyle } from "styled-components";
import { oval } from "../assets/images";

const GlobalStyle = createGlobalStyle`

/*----------------------------
	xs: 0px
	sm: 600px
	md: 900px
	lg: 1200px
	xl: 1536px
----------------------------*/

/*----------------------------
	Global styles
----------------------------*/

// Layout
	.container {
		

		h5 {
			@media (max-width: 1199px) {
				font-weight: 700 !important;
				font-size: 16px !important;
				line-height: 24px !important;
				text-align: center !important;
				margin-bottom: -7px !important;
			}
		}
	}
	.textContainer {
		width: 1075px;
		margin: auto;
		@media (max-width: 1075px) {
			width: 100%;
		}
	}
	.padding {
		padding: 4.25rem 0;
		@media (max-width: 600px) {
    	padding: 3rem 0;
		}
	}
	.linkClass {
		color: white!important;
		text-decoration: none!important;
	}
	.colorPrimary {
		color: #6D4AFF;
	}
	.colorSecondary {
		color: #00ACED;
	}
	.textHeader {
		text-transform: capitalize;
		text-align: center;
		font-size: 46px;
		line-height: 56px;
		font-weight: bold;
		margin: auto;
		padding: 64px 0 120px;
		width: fit-content;
    @media (max-width: 1200px) {
			font-size: 30px;
			line-height: 38px;
			padding: 0px 0 48px;
    }
    @media (max-width: 900px) {
			font-size: 24px;
			line-height: 20px;
    }
	}
	.subHeader {
		text-transform: capitalize;
		text-align: center;
		font-size: 24px;
		line-height: 39px;
		font-weight: bold;
		margin: 0;
		padding: 0;
    @media (max-width: 1200px) {
			font-size: 22px;
			line-height: 33px;
    }
    @media (max-width: 900px) {
			font-size: 20px;
			line-height: 28px;
    }
	}
	.MuiDrawer-paper {
		background-color: #121122!important;
	}
	.imgSort {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		@media (max-width: 1200px) {
			margin: 0!important;
			flex-direction: column;
		}
	}
	.gridSpread:not(:first-child) {
		text-align: center;
	}
	.gridSpread:nth-last-child(1) {
		text-align: right;
	}
	@media (max-width: 1200px) {
		.gridSpread, .gridCenter {
			text-align: center!important;
		}
	}

	.gradientClass {
    background: linear-gradient(248.54deg, #45D7D9 10.13%, #8383FF 67.36%, #976DFF 99.98%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
    width: fit-content;
	}

// Navbar
	nav {
		position: fixed;
		width: 100%;
		left: 0;
		background: #121122;
		.container{
			width: 1200px;
			margin-left:calc(50vw - 600px);
			@media (max-width: 1199px) {
				width: 86%;
				margin-left:7%;
			}
		}
		z-index: 10;
		&#mobileNav {
			position: sticky;
			height: 55px;
			
			& > div {
				height: 100%;
				display: flex;
				align-items: center;
				justify-content: space-between;
			}
			& img {
		    top: 5px;
		    position: relative;
				height: auto;
				width: 100px;
			}
		}
	}
	#navbar {
		display: flex;
		height: 91px;
		background-color: #121122;
		img {
			position: absolute;
			top: 21px;
			height: 48px;
			width: auto;
		}
		.linkContainer {
			display: flex;
			justify-content: flex-end;
			align-items: center;
			margin: auto;
			margin-right: 0;
			.linkClass {
				cursor: pointer;
				text-transform: capitalize;
				margin: 0 1.1rem 0;
				font-size: 14px;
				&:hover {
					color: #7BEFFF!important;
				}
			}
		}
		.navButton {
			margin: auto 0;
			// margin-left: 3rem;
		}
		#languageButton {
			margin-left: auto;
			cursor: pointer;
			display: flex;
			align-items: center;
			p {
				margin: 0;
				margin-left: .25rem;
				font-size: 14px;
				text-transform: capitalize;
			}
		}
		@media (max-width: 1200px) {
			.linkContainer {
				.linkClass {
					font-size: 12px;
					margin: 0 .5rem 0;
				}
			}
		}
	}
	#basic-menu {
		li {
			text-transform: capitalize;
			font-size: 14px!important;
		}
	}

// Home
	#home {
		display: flex;
		align-items: center;
		@media (max-width: 900px) {
			flex-direction: column;
		}
		.container {
			position: absolute;
			top: 33%;
			left: 0%;
			width:84%;
			@media (min-width: 900px) {
				left:7%;
				@media (min-width: 1200px) {
					left:calc(50vw - 600px);
				}
			}
			
			z-index: 10;
			max-width: 625px;
			.appText {
				button {
					font-weight: 600;
					font-size: 20px;
					line-height: 18px;
					padding: 16px 24px;
					width: 186px;
					height: 50px;
					@media (max-width: 1200px) {
						font-weight: 500;
						font-size: 16px;
						line-height: 24px;
						// padding: 12px 108.5px;
						width: 313px;
					}
				}
			}
	    @media (min-width: 1600px) {
				max-width: 45vw;
	    }
			@media (max-width: 900px) {
				.appText {
					text-align: center;
					button {
						min-height: 44px;
						p {
							font-size: 16px!important;
						}
					}
				}
				position: relative;
				top: 0;
			}
		}
		h1,h5,h6{
			text-shadow: 0px 0px 8px rgba(0, 0, 0, 0.25) !important;
		}
		h5 {
			margin: 0;
			font-weight: 700;
			font-size: 24px;
			line-height: 34px;
		}
		h1 {
			margin: 16px 0;
			font-weight: 700;
			font-size: 48px;
			line-height: 56px;
	    @media (min-width: 1600px) {
				font-size: 3.5vw;
	    	line-height: 4vw;
	    }
			@media (max-width: 1100px) {
				font-size: 40px;
				line-height: 50px;
			}
			@media (max-width: 900px) {
				font-weight: bold;
				font-size: 24px;
				line-height: 30px;
				text-align: center;
			}
		}
		h6 {
			margin: 0;
			max-width: 800px!important;
			font-weight: normal;
			font-size: 20px;
			line-height: 33px;
			white-space: break-spaces;
			@media (min-width: 1600px) {
			  font-size: 24px;
			}
			// @media (max-width: 1100px) {
			// 	font-size: 16px;
			// 	line-height: 24px;
			// 	width: 250px;
			// 	margin: auto;
			// }
			@media (max-width: 900px) {
				font-size: 14px;
				line-height: 22px;
				text-align: center;
			}
		}
		button {
			margin-top: 2.5rem;
			@media (max-width: 1199px) {
				margin-top: 22px;
			}
		}
	}

// Value
	#value {
		position: relative;
		z-index: 1;
	}
	.celestialImg {
		text-align: center;
		margin: auto;
		position: relative;
		img {
			position: relative;
			z-index: 1;
			top: 0;
			left: 0;
			right: 0;
			right: 0;
			margin: auto;
			width: 50vw;
			height: auto;
			max-width: 1125px;
		}
		&:before {
			display: block;
			position: absolute;
			content: '';
			height: 100%;
			width: 100%;
			background-image: url(${oval});
			background-size: 23vw;
	    background-repeat: no-repeat;
	    background-position: center;
	    top: -14.5px;
	    left: -7px;
	    z-index: -5;
	    animation-name: celestial-animate;
			animation-duration: 20s;
			animation-timing-function: linear;
			animation-iteration-count: infinite;
			@media (min-width: 2225px) {
				background-size: 500px;
				top: -14px;
				left: 13px;
			}
		}
	}
	#value .textContainer {
		.gridItem {
			width: 290px!important;
			position: absolute;
			white-space: pre-line;
			z-index: 1;
			top: 0;
			img {
				margin-top: 1.5rem; 
			}
			h3 {
				font-size: 18px;
				margin-bottom: 0rem;
    		line-height: 28px;
			}
			ul {
				list-style: none;
				padding: 0;
			}
			li {
				font-size: 13.5px;
    		line-height: 22px;
    		color: #B0B5C4;
    		margin-bottom: .5rem;
			}
			@media (max-width: 900px) {
				position: relative;
				left: 0!important;
				top: 0!important;
				text-align: center;
		    margin: auto;
			}
		}
		.MuiGrid-root {
			&:nth-child(even) {
				@media (min-width: 900px) {
					.gridItem {
						margin-left: auto;
					}
				}
			}
		}
		.value1 {
			top: 20%;
			left: -2.5%;
		}
		.value2 {
			top: 20%;
			right: -5%;
		}
		.value3 {
			top: 55%;
			left: -2.5%;
		}
		.value4 {
			top: 55%;
			right: -5%;
		}
		@media (max-width: 1200px) {
			.value1, .value3 {
				left: 5%;
			}
			.value2, .value4 {
				left: 7.5%;
				right: 2.5%;
			}
			.value1, .value2 {
				top: 15%;
			}
			.value3, .value4 {
				top: 50%;
			}
		}
		@media (min-width: 2049px) {
			.value1, .value3 {
				left: -7.5%;
			}
			.value2, .value4 {
				right: -12.5%;
			}
		}
	}

// Strategy
	#strategy {
		max-width: 1250px;
		margin: auto;
		.cardContainer:first-of-type {
			justify-content: center;
			@media (max-width: 600px) {
				padding: 0.5rem;
			}
		}
		.cardContainer:last-of-type {
			justify-content: center;
			.strategyCard {
				margin: 1rem 21px;
			}
			@media (max-width: 600px) {
				justify-content: space-between;
			}
		}
	}
	.cardContainer {
		display: flex;
		flex-wrap: wrap;
		gap: 57px;
		.strategyCard:nth-child(odd) {
			&:before {
				background: linear-gradient(259.88deg, #0EF7F8 0%, #95C0FF 50.25%, #AD60FF 100%);
				border-radius: 14px;
			}
		}
		.strategyCard:nth-child(even) {
			&:before {
				background: linear-gradient(259.88deg, #0EF7F8 0%, #95C0FF 50.25%, #AD60FF 100%);
			}
		}
		@media (max-width: 990px) {
    	justify-content: center!important;
		}
		@media (max-width: 900px) {
    	margin-bottom: 2rem;
		}
	}
	.strategyCard {
		padding: 23px 37px 19px 26px;
		cursor: pointer;
		height: 160px;
		width: 257px;
		background-color: #29283B;
		transition: 0.5s;
		&:hover {
			background-color: #060516;
			transition: 0.5s;
		}
		border-radius: 12px;
		margin: 1rem 8px;
		position: relative;
		display: flex;
		flex-direction: column;
		.left-image-icon, .right-image-icon{

			height: 42px;
			position:relative;
			z-index:1;
			margin-bottom:10px;
		}
		.right-image-icon{
			margin-left:-6px;
			z-index:0;
		}
		.title{
			font-weight: 600;
			font-size: 18px;
			line-height: 14px;
		}
		.dapp{
			margin-top:16px;
			margin-bottom:14px;
			padding: 4px 12px;
			background: rgba(255, 255, 255, 0.1);
			border-radius: 8px;
			width: fit-content;
			font-weight: 500;
			font-size: 14px;
			line-height: 20px;
			display: flex;
			align-items: center;
			img{
				height: 28px;
				margin-right:4px;
			}
		}
		.gradient-text{
			font-weight: 500;
			font-size: 18px;
			// line-height: 16px;
			background: linear-gradient(6deg, #B182FF 14.11%, #59C1FB 56.23%, #0EF7F8 85.68%);
			-webkit-background-clip: text;
			-webkit-text-fill-color: transparent;
		}
		.background {
			width: 84px;
			height: 84px;
			position: absolute;
			right:37px;
			top:46px;
		}
	
	}
	.strategyCard:before {
		content: '';
		z-index: -10;
		position: absolute;
		top: -2px;
		left: -2px;
		height: calc(100% + 4px);
		width: calc(100% + 4px);
		display: block;
		border-radius: 9px;
	}

// How
	#how {
		text-align: center;
		.textContainer > div {
			position: relative;
		}
		.textContainer {
			position: relative;
			& > img {
				width: 50px;
				position: absolute;
				top: 700px;
				opacity: 0;
			}
		}
		.howContainer {
			 img {
				margin-top: 2rem;
				width: 840px;
				height: auto;
			}
			@media (max-width: 1150px) {
				img {
					width: 500px;
				}
			}
			@media (max-width: 900px) {
		    display: flex;
		    flex-direction: column-reverse;
		    img {
			    width: 90%;
			    margin: 2rem auto;
		    }
		    & > div {
					display: flex;
					flex-direction: column;
					margin: auto;
		    }
			}
		}
		.howClass {
			display: flex;
			align-items: flex-start;
			position: absolute;
			.item {
				background: #6D4AFF;
				border: 1px solid #000000;
				height: 28px;
				width: 28px;
				border-radius: 50%;
				display: flex;
				align-items: center;
				justify-content: center;
				margin: 0 .5rem;
				position: relative;
				&:before {
					position: absolute;
					content: '';
					top: -1px;
					left: -5px;
					display: block;
					height: 28px;
					width: 28px;
					background: #3F35EF;
					border: 1px solid #000000;
					z-index: -1;
					border-radius: 50%;
				}
				@media (max-width: 600px) {
					height: 25px;
					width: 25px;
					&:before {
						height: 25px;
						width: 25px;
					}
				}
			}
			.value {
				font-size: 14px;
				line-height: 22px;
				text-align: left;
				width: 200px;
				white-space: break-spaces;
				@media (max-width: 1150px) {
					font-size: 11px;
				}
				@media (max-width: 900px) {
					width: 290px;
					font-size: 15px;
					margin-bottom: 2rem;
					padding-left: .5rem;
				}
			}
			&.how1 {
				top: 400px;
			}
			&.how2 {
				right: -25px;
				top: 200px;
			}
			&.how3 {
				right: -25px;
				top: 350px;
			}
			&.how4 {
				top: 600px;
			}
			@media (max-width: 1150px) {
				&.how1 {
					top: 225px;
					left: 7.5%;
				}
				&.how2 {
					top: 150px;
					right: 4%;
				}
				&.how3 {
					top: 240px;
					right: 7.5%;
				}
				&.how4 {
					top: 400px;
					left: 11.5%;
				}	
			}
			@media (max-width: 900px) {
		    position: relative;
		    top: 0px!important;
		    right: 0px!important;
		    left: 0px!important;
			}
		}
	}

// Offering
	#offering {
		.offeringClass {
			background-color: #212030;
			padding: 24px 24px 24px 30px;
			margin: auto;
			margin-bottom: 2rem;
			border-radius: 10px;
			overflow: hidden;
			h5 {
				font-weight: 700;
				font-size: 18px;
				line-height: 27px;
				text-transform: capitalize;
				margin: 0;
				padding: 0;
			}
			h2{
				font-weight: 600;
				font-size: 28px;
				line-height: 42px;
				margin: 0;
				padding: 0;
				@media (min-width: 1200px) {
					margin-bottom:8px;
				}
				@media (max-width: 1200px) {
					margin-top:4px;
				}
			}
			p {
				font-weight: 400;
				font-size: 16px;
				line-height: 24px;
				margin: 0;
				padding: 0;
				@media (max-width: 1200px) {
					margin-top:12px;
				}
			}
		
			img {
				width: 502px;
			}
			
			@media (max-width: 1100px) {
				width: 97.5%;
				p {
					font-size: 14px;
				}
			}
			@media (max-width: 900px) {
				background-color: transparent;
				padding: 1rem;
				margin-bottom: 1rem;
				border-radius: 0px;
				h2 {
					font-size: 20px;
					line-height: 22px;
					text-align: center;
				}
				h5 {
					font-size: 18px;
					line-height: 22px;
					text-align: center;
				}
				p {
					font-size: 14px;
					line-height: 26px;
					text-align: center;
				}
				.grid {
					img {
						margin-top: 1.5rem;
						background-color: #212030;
						border-radius: 10px;
						width: 100%;
					}
				}
			}
			.paddingRight{
				padding-right:56px;
				@media (max-width: 900px) {
					padding-right:0px;
				}
			}
		}
	}

// Token
	#token {
		.textContainer {
			position: relative;
		}
		.content {
			text-align: center;
			p {
				margin: auto;
				margin-bottom: 3rem;
				font-size: 20px;
				line-height: 33px;
				max-width: 925px;
				@media (max-width: 1200px) {
					font-size: 16px;
					line-height: 26px;
					padding: 0 3rem;
				}
				@media (max-width: 900px) {
					font-size: 12px;
					line-height: 20px;
					padding: 0 1.5rem;
					margin-bottom: 0;
				}
			}
			h2 > span {
				font-size: 28px;
				margin: auto .5rem;
			}
		}
		.tokenList {
			display: flex;
			align-items: center;
			justify-content: space-around;
			width: 100%;
			height: 100%;
			padding-top: 6rem;
			flex-wrap: wrap;
			.body {
				& > div {
					padding: 1rem .75rem;
					padding-bottom: 0;
					p:first-of-type {
						padding: 0;
						margin: 0;
						font-weight: bold;
						font-size: 18px;
						@media (max-width: 900px) {
							font-size: 15px;
							line-height: 14px;
						}
					}
					p:last-of-type {
						margin-top: .75rem;
						@media (max-width: 900px) {
							font-size: 12px;
							line-height: 16px;
						}
					}
				}
			}
			@media (max-width: 1200px) {
				justify-content: center;
			}
			@media (max-width: 900px) {
		    padding-top: 3rem;	
			}
		}
		@media (max-width: 1200px) {
			margin-bottom: 0rem;
		}
	}

// Stats
	#stats {
		background-color: #1f1f2f;
		text-align: center;
		h1 {
			font-size: 34px;
			font-weight: bold;
			color: #32c4fe;
		}
		p {
			padding: 0 2rem;
			font-size: 16px;
		}
	}

// Team
	#about {
		padding: 2rem 0 0;
		.teamContainer {
			margin-top: 2rem;
			display: flex;
			justify-content: space-evenly;
			flex-wrap: wrap;
		}
		.teamClass {
			position: relative;
			width: 333px!important;
			display: flex;
			align-items: center;
			flex-direction: column;
			text-align: center;
			width: 100%;
			.teamclass-img{
				height:115px;
				width:115px;
				border-radius: 50%;
				cursor: pointer;
			}
			.teamclass-in{
				display: none;
			}
			.teamclass-img:hover{
				transition: 0.5s;
				background-color: rgba(187, 187, 187, 0.7);
				.teamclass-in{
					transition: display 0.5s;
					display: block;
					position: absolute;
					left: 50%;
					top: 0%;
					transform: translate(-45%, 40%);
					z-index: 9;
				}
			}
			img {
				width: 115px;
				object-fit: cover;
				border-radius: 50%;
				cursor: pointer;
				position:relative;
				z-index:-1;
			}
		
			a {
				display: flex;
				align-items: center;
				text-decoration: none;
				p {
					font-size: 20px;
					font-weight: 400;
					opacity: 1;
					padding: 0;
				}
				img {
					margin-left: .5rem;
				 	width: 32px;
				 	height: 32px;
				}
			}
			p {
				font-weight: normal;
				font-size: 18px;
				line-height: 30px;
				text-align: center;
				letter-spacing: -0.375px;
				color: #FFFFFF;
				opacity: 0.6;
			}
		}
		@media (max-width: 1200px) {
			.teamClass {
				p {
					font-size: 16px;
					line-height: 25px;
				}
			}
		}
		@media (max-width: 900px) {
			.teamContainer {
	    	justify-content: center;
	    }
			.teamClass {
				margin-bottom: 2rem;
				& > img {
					width: 85px;
				}
				p {
					font-size: 14px;
					padding: 0 2rem;
					line-height: 20px;
				}
			}
		}
	}

// Featured
	#featured {
		.textContainer {
			width: 1200px;
			@media (max-width: 1200px) {
				width: 1075px;
			}
			@media (max-width: 1075px) {
				width: 100%;
			}
		}
	}

// Partners
	#partners {
		.textContainer {
			width: 1200px;
			@media (max-width: 1200px) {
				width: 1075px;
			}
			@media (max-width: 1075px) {
				width: 100%;
			}
		}
		.sponsorClass {
			margin-top: 2rem;
			img {
				padding: 1rem;
				height: auto;
				text-align: center;
				object-fit: contain;
			}
			p {
				padding: 1rem;
				margin: 0;
				width: 175px;
				font-size: 30px;
				font-weight: 700;
				letter-spacing: -1px;
				text-align: center;
				&.textSmall {
					font-size: 1.25rem;
				}
			}
		}
		@media (max-width: 600px) {
			.sponsorClass {
				& > div {
					height: 75px;
				}
				img {
					width: 105px;
					height: auto;
				}
				p {
					width: 105px;
					font-size: 18px;
					&.textSmall {
						font-size: .85rem;
					}
				}
			}
		}
	}

// Footer
	footer {
		.container{
			@media (min-width: 1199px) {
				margin-left: calc(50vw - 600px);
				width: 1200px;
			}
		}
		& .container > div {
			display: flex;
			width: 100%;
			margin-top: 33px;
			justify-content: space-between;
		}
		.img {
			height: auto;
			width: 129px;
			@media (max-width: 1199px) {
				margin: auto;
				display: block;
				margin-bottom: 36px;
			}
		}
		.img-small{
			width: 24px;
			height: auto;
			margin-left: 40px;
			@media (max-width: 1199px) {
				width: 20px;
			}
			cursor: pointer;
			&:hover{
				filter: invert(57%) sepia(178%) saturate(530%) hue-rotate(158deg) brightness(98%) contrast(75%)
			 }
		}
		.foot-container{
			@media (min-width: 1199px) {
				width: 650px;
				float:right;
			}
			width: 100%;
		
		}
		.footer-menu-grid{
			display: grid;
			grid-template-columns: 38% 38% 24%;
			@media (max-width: 1199px) {
				padding:0px 25px 0px 50px;
			}
		}
		.footer-title{
			font-weight: 700;
			font-size: 18px;foot-container
			line-height: 27px;
			@media (max-width: 1199px) {
				font-size: 14px;
				line-height: 21px;
			}
		}

		.float-right{
			float:right;
			width: 100%;
		}

		.foot-container-bottom{
			width:100%;
			display: flex;
			flex-direction: row-reverse;
			margin:45px 0px;
			align-items: center;
			@media (max-width: 1199px) {
				flex-direction: column-reverse;
				margin:25px 0px;
			}
			.footer-display{
				display: flex;
				flex-direction: row;
				align-items: center;
			}
			.social-icon{
				@media (max-width: 1199px) {
					margin-bottom: 20px;
					margin-left: -20px;
					
				}
			}
		}
		.grid{
			display: grid;
			grid-template-columns: 25% 25% 25% 25%;
		}
		.linkContainer {
			display: grid;
			float:right;
			grid-template-columns: auto auto auto;
			@media (max-width: 1199px) {
				display: flex;
				justify-content: center;
				float:none;
			}
		}
		.footerChild {
			margin-left:24px;
			display: flex;
			height: 100%;
			& hr {
		    margin: auto;
		    margin-left: 1rem;
		    height: 16px;
		    border: 0;
		    border-left: 1px solid white;
		    opacity: .1;
			}
		}
		.margin{
			margin: 45px 0px;
			@media (max-width: 1199px) {
				margin: 10px 0px;
			}
		}
		.margin-left{
			@media (max-width: 1199px) {
				width:50%;
				margin-left:25%;
			}
		}
		.flex{
			display: flex;
			align-items: center;
			
		}
		a {
			color: white;
			text-decoration: none;
			text-transform: capitalize;
			font-weight: 400;
			font-size: 16px;
			line-height: 24px;
			margin-top: 6px;
			display: block;
			 &:hover{
				color: rgba(123, 239, 255, 1);
			 }
			@media (max-width: 1199px) {
				font-size: 12px;
				line-height: 18px;
			}
		}
		p {
			text-transform: capitalize;
			font-size: 12px;
			margin:0;
		}
		.iconContainer {
			img {
				width: 30px;
			}
			a {
				margin: 0;
				margin-right: 1.25rem;
			}
			a:last-of-type {
				margin-right: 0;
			}
		}
		@media (max-width: 1150px) {
			.footerContainer {
				& .iconContainer, & .linkContainer {
					min-width: auto;
				}
			}
		}
		@media (max-width: 900px) {
			padding-bottom: 1rem;
			height: auto!important;
			.container {
				padding: 0;
				& > div {
					display: flex;
					flex-direction: column;		
				}
			}
			.footerChild {
				margin: auto 0.5rem auto 0;
				& hr {
					margin-left: .5rem;
				}
			}
		}
		@media (max-width: 600px) {
			.container {
				& > div {
					p, a {
						font-size: 10px;
					}
				}
			}	
		}
	}

// Notified
	#notified {
		.ml-field-group {
			position: relative;
			padding: .1rem;
			&:before {
				content: '';
				position: absolute;
				height: 102.5%;
				width: 100.5%;
				top: -.5px;
				left: -1px;
				border-radius: 6px;
				background: linear-gradient(359.97deg,#32C5FF 0.03%,#8DD1FB 52.23%,#E4A677 99.97%);
			}
			input{
				position: relative;
				border: 0!important;
			}
		}
		@media (max-width: 900px) {
			margin-bottom: 5rem;
			.inputClass {
				width: 350px;
				input {
					width: 300px;
				}
			}
		}
	}

// Terms
	#terms {
		margin: 5% auto auto;
		@media (max-width: 600px) {
			margin-bottom: 15%;
		}
	}

// Privacy
	#privacy {
		margin: 5% auto auto;
		@media (max-width: 600px) {
			margin-bottom: 15%;
		}
	}

// Mediakit
	#mediakit {
		margin: 5% auto auto;
		padding: 0 2rem;
		.logoColorClass {
			margin-top: 2rem;
			display: flex;
			justify-content: space-between;
			width: 100%;
			& > div {
				width: 30%;
			}
			span {
				display: flex;
				height: auto;
				margin-top: .75rem;
				cursor: pointer;
				width: max-content;
				img {
					height: 100%;
				}
				p {
					margin: 0;
					padding: 0;
					margin-left: .5rem;
				}
			}
		}
		.colorClass {
			height: 140px;
			width: 100%;
		}
		@media (max-width: 750px) {
			.logoColorClass {
				display: block;
				& > div {
					width: 100%;
					margin-bottom: 2rem;
				}
			}
			.colorClass {
				height: 200px;
			}
		}
		@media (max-width: 600px) {
			margin-bottom: 15%;
		}
	}
`;

export default GlobalStyle;
