import React from 'react'

export default function TextBox(props) {

	const { title, list, img } = props.t;

	return (
		<div className={`value${props.i} gridItem`}>
			{ props.width < 900 && <img src={img} width="65" alt={title} /> }
			<h3>{title}</h3>
			<ul>
				{
					list.map((l, i) => (
						<li key={i}>{l}</li>
					))
				}
			</ul>
		</div>
	)
}