import * as React from 'react';
import { MnemonicKey, AnchorEarn, DENOMS, CHAINS, NETWORKS } from '@anchor-protocol/anchor-earn';

const TokenContext = React.createContext();

export function useToken() {
  return React.useContext(TokenContext);
}

const TokenProvider = ({children}) => {

	const [anchor, setAnchor] = React.useState(0);

	React.useEffect(() => {

		const account = new MnemonicKey({
			mnemonic: "figure alter shrimp pottery bottom cigar icon field pole feel project sheriff leaf guide proud hidden book congress slot deer jelly cushion airport cross",
		});

		const anchorEarn = new AnchorEarn({
		  chain: CHAINS.TERRA,
		  network: NETWORKS.COLUMBUS_5,
		  address: account.accAddress,
		});

		async function getInfo() {
			const market = await anchorEarn.market({
				currencies: [DENOMS.UST],
			});
			return market;
		};
		getInfo().then((data) => {
			let val = data.markets[0];
			setAnchor(val);
		});

	},[]);

  return (
    <TokenContext.Provider value={{
    	anchor,
    }}>
      {children}
    </TokenContext.Provider>
  )
}

export { TokenProvider,  TokenContext };